import { useState, useEffect,lazy,Suspense } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Application from "./Components/Application";
import { BrowserRouter as Router, Switch, Route ,Redirect } from "react-router-dom";
import { auth, db } from "./Firebase/Firebase";
import nameMapping from "./Data/name_mapping";

// import Chat from "./Components/Chat";
// import Login from "./Components/SignUp";
// import Home from "./Components/Home";
import Signin from "./Components/Signin"
import Register from "./Components/Register"
// import Entrance from "./Components/Entrance"
// import Qrcode from "./Components/Qrcode"
// import KeyName from "./Components/KeyName";
// import Badword from "./Components/Badword";
// import Namemapping from "./Components/Namemapping"
import "./App.css";

const Chat = lazy (()=> import("./Components/Chat"));
const Login = lazy (()=> import("./Components/SignUp"));
const Home  = lazy (()=> import("./Components/Home"));
// const Signin = lazy (()=> import("./Components/Signin"));
// const Register = lazy (()=> import("./Components/Register"));
const Entrance = lazy (()=> import("./Components/Entrance"));
const Qrcode = lazy (()=> import("./Components/Qrcode"));
const KeyName = lazy (()=> import("./Components/KeyName"));
const Badword = lazy (()=> import("./Components/Badword"));
const Namemapping = lazy (()=> import("./Components/Namemapping"));



const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    backgroundColor: "#22273b !important",
    height: "100vh",
  },
}));

function App() {
  const classes = useStyles();
  const [user, setUser] = useState(null);
  const [mainEvent, setMainEvent] = useState('');
  const [badWordsData, setBadWordsData] = useState('');
  const [allSettings,setAllSettings] = useState({
  });
  useEffect(() => {
    auth.onAuthStateChanged((user) => {
      if (user) {
        db.collection("users")
          .doc(user.uid)
          .get()
          .then((doc) => {
            if (doc.exists) {
            } else {
              const details = {
                name: user.displayName,
                // displayName: user.displayName.split(" ")[0],
                photoURL: user.photoURL,
                email: user.email,
                uid: user.uid,
              };
              db.collection("users")
                .doc(user.uid)
                .set(details)
                .then((res) => {

                })
                .catch((err) => {
                  console.log(err);
                });
            }
          })
          .catch((err) => {
           
          });

        setUser(user.uid);
      } else {
        setUser(null);
      }
    });
  }, []);
  useEffect(()=>{
    db.collection("projectSettings")
      .doc("settings")
      .onSnapshot((snapshot) => {
        let eventName = snapshot.data().mainEvent;
        setMainEvent(
          eventName
        )
      })
  },[])

  useEffect(()=>{
    db.collection("projectSettings")
      .doc("badWords")
      .onSnapshot((snapshot) => {
        let badWordsData = snapshot.data().badWords;
        setBadWordsData(
          badWordsData
        )
      })
  },[])

  useEffect(() => {
    if(mainEvent){
        db.collection("events")
            .doc(mainEvent)
            .onSnapshot((snapshot)=>{
                let settings = snapshot.data();
                setAllSettings(
                    settings
                )
            })

    }
}, [mainEvent])



  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/" exact render={(props)=>(
            <Suspense fallback={<div>Loading...</div>}>
              <Entrance allSettings={allSettings} mainEvent={mainEvent} {...props}/>
              </Suspense>
            )}/>
          <Route path="/keyname" exact render={(props)=>(
                        <Suspense fallback={<div>Loading...</div>}>
            <KeyName allSettings={allSettings} badWordsData={badWordsData} mainEvent={mainEvent} {...props}/>
            </Suspense>
            )}/>
          <Route path="/qrcode" exact render={(props)=>(
                                    <Suspense fallback={<div>Loading...</div>}>
            <Qrcode allSettings={allSettings} mainEvent={mainEvent} {...props}/>
            </Suspense>
            )}/>
          <Route path="/namemapping/:id" render={(props)=>(
              <Suspense fallback={<div>Loading...</div>}>
                <Namemapping allSettings={allSettings} mainEvent={mainEvent} {...props}/>
              </Suspense>
            )}/>
          {!user ? (
            <>
            <Route path="/register" exact component={Register} />
            <Route path="/signin" exact component={Signin} />
            {/* <Route path="/login" exact component={Login} /> */}
            {/* <Redirect to="/signin"/> */}
            </>

        ) : (
          <>
          <div className={classes.root}>
            <Application uid={user} />
            <main className={classes.content}>
              <div className={classes.toolbar} style={{ minHeight: "50px" }} />
              
                <Route path="/home" render={(props)=>(
              <Suspense fallback={<div>Loading...</div>}>
                <Home/>
              </Suspense>
            )} exact/>
                <Route path="/badword" render={(props)=>(
              <Suspense fallback={<div>Loading...</div>}>
                <Badword />
              </Suspense>
            )} />
                <Route path="/event/:id" render={(props)=>(
              <Suspense fallback={<div>Loading...</div>}>
                <Chat />
              </Suspense>
            )}/>
            </main>
          </div>
          <Redirect to="/home"/>
          </>
        )}
            <Redirect to="/"/>
        </Switch>

      </Router>
    </div>
  );
}

export default App;
